@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
*{
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
.wrapper {
  width: 28%;
  margin: auto;
  margin-top: 20px;
  border: 1px solid gray;
  height: auto;
  overflow: auto;
}
.wrapper h4{
  background-color: #dddddd;
  padding: 10px 20px;
  margin: 0;

}
input {
  padding: 10px 20px;
  width: 100%;
  border: none;
  border-bottom: 1px solid gray;
}
input:focus {
  outline:none;
}
.emptyList {
  text-align: center;
  color: rgb(214, 210, 210);

}
.collection-item {
  padding: 15px 20px;
  border-bottom: 1px solid gray;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.listItems > .collection-item:nth-last-child(1){
  border-bottom: none;

}

#del {
  cursor: pointer;
  margin-left: 20px;
}

#fav > img ,#del >img{
  width: 20px;
  height: 20px;
  vertical-align: middle;
}
.pagination {
  display: flex;
  list-style: none;
  width: 28%;
  margin: auto;
  padding: 0;
  
}
.page-item {
  padding: 5px 10px;
  border: 1px solid grey;
 
}
.page-item >a{
  text-decoration: none;
 
}
#del ,#fav{
  border: 1px solid grey;
  padding: 5px;
  border-radius: 3px;

}

.actionBtn {
  flex-basis: 40%;
  margin-left: 10px;
  text-align: right;
}
.friendName {
  flex-basis: 60%;
  word-break: break-word;

}
.friendName >h6{
  font-weight: bold;
  font-size: 1.4em;
}
.friendName >p{
  counter-reset: grey;
  font-size: 12px;
}
